<template>
<div class="card-group container-position col-12">
    <div v-if="floor_maps.length > 0" class="row">
  <div v-for="map in floor_maps" :key="map.id" class="col-6">
  <div @click="seeDetails(map)" class="card map-card">
      <div class="card-body-map">
        <h5 class="card-title">{{map.name}}</h5>
        <p class="card-text">{{map.markers.length}} keys</p>
       </div>
    <img class="card-img-top" :src="'data:image/png;base64,'+ map.picture" alt="Card image cap">
  </div>
    </div>
</div>
</div>
</template>

<script>
import mapRepository from "@/repository/map";
import lodash from "lodash";
import store from "@/flulux/store";
export default {
  data() {
    return {
      floor_maps: [],
      store: store
    };
  },
  async mounted() {
    await this.getAllFloorMaps();
  },
  methods: {
    seeDetails(p) {
      this.store.state.map.currentFloor = p;
      this.$router.push("Keychain-map-details");
    },
    async getAllFloorMaps() {
      const maps = await mapRepository.getAllFloorMaps();
      maps.map(async item => {
        const details = await mapRepository.getOneFloorMaps(item.id);
        this.floor_maps.push({
          ...item,
          markers: JSON.parse(item.positions),
          picture: details.base64_picture
        });
      });
    }
  }
};
</script>

