<template>
<div>
<div ref="content" class="content" v-bind:style="{ backgroundImage: 'url(' + 'data:image/png;base64,'+ store.state.map.currentFloor.picture + ')', transform: 'scale(' + this.scale +')' }">
<img draggable="true" v-on:dragstart="drag(event, i)" v-for="(marker, i) in store.state.map.currentFloor.markers" :key="marker" :style="{left: marker.x + '%', top: marker.y + '%', position: 'absolute', 'margin-left': '-4em'}" @click="dragKey()" src="@/assets/havr-icon/key-marker.svg" class="key-marker"></div>
<nav class="zoom">
 <ul class="pagination">
    <li @click="zoom(true)"  class="page-item disabled zoom-paginator">
      <a class="page-link text-secondary" href="#" tabindex="-1">+</a>
    </li>
    <li @click="zoom(false)"  class="page-item disabled zoom-paginator">
      <a class="page-link text-secondary" href="#" tabindex="-1">-</a>
    </li>
  </ul>
</nav>
</div>

</template>

<script>
import store from "@/flulux/store";
import fabric from "fabric";
import $ from "jquery";
import notify from "vue-notification";
export default {
  data() {
    return {
      store: store,
      scale: 1,
      currentMarkerDrag: null
    };
  },
  mounted() {
    this.showDrawer();
    var vm = this;
    this.$refs.content.addEventListener(
      "dragend",
      function(e) {
        const offset = $(this).offset();
        const relativeX = e.pageX - offset.left;
        const relativeY = e.pageY - offset.top;
        if (
          relativeY / 1000 * 100 < 0 ||
          relativeY / 1000 * 100 > 100 ||
          relativeX / 1000 * 100 < 0 ||
          relativeX / 1000 * 100 > 100
        ) {
           vm.store.state.map.currentFloor.markers.splice(vm.currentMarkerDrag, 1)
          return vm.$notify({
            message: "Marker has been replaced in the list",
            type: "info",
            horizontalAlign: "right",
            timeout: 2000,
            verticalAlign: "top"
          });
         
        }
        vm.store.state.map.currentFloor.markers[vm.currentMarkerDrag].y =
          relativeY / 1000 * 100;
        vm.store.state.map.currentFloor.markers[vm.currentMarkerDrag].x =
          relativeX / 1000 * 100;
      },
      false
    );
  },
  methods: {
    async showDrawer(file) {
      this.store.actions.OPEN_GROUP_MAP_DRAWER(true);
    },
    drag(ev, i) {
      this.currentMarkerDrag = i;
    },
    zoom(param) {
      if (param) {
        this.scale = this.scale + 0.2;
      } else {
        this.scale = this.scale - 0.2;
      }
    }
  }
};
</script>

<style scoped>
.content {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  background-repeat: no-repeat;
  width: 1000px;
  background-size: 100% 100%;
  height: 1000px;
  left: 5%;
  overflow: auto;
  top: 5em;
}

.zoom-paginator {
  width: 40px;
  text-align: -webkit-center;
}

.zoom {
  position: fixed;
  bottom: 0;
  right: 0;
  margin-right: 3%;
}
</style>
